import React from 'react';
import PropTypes from 'prop-types';
import { Panel, ListItem, Button, Group, Div, Avatar, PanelHeader } from '@vkontakte/vkui';
//import request from'request';

var mmm;

var request = require('request'),
cheerio = require("cheerio"),
url = "http://vhost86040.cpsite.ru/getmybal.php/";

request(url, function (error, response, body) {
    if (!error) {
        var $ = cheerio.load(body),
			temperature = $('#msa').text();
			mmm = temperature;
    } else {
        console.log("Произошла ошибка: " + error + body);
    }
});

const Home = ({ id, go, fetchedUser }) => (
	<Panel id={id}>
		<PanelHeader>MSA APP</PanelHeader>
		{fetchedUser &&
		<Group title="Данные аккаунта">
			<ListItem
				before={fetchedUser.photo_200 ? <Avatar src={fetchedUser.photo_200}/> : null}
				description={"Количество баллов:" + mmm}
			>
				{`${fetchedUser.first_name} ${fetchedUser.last_name}`}
			</ListItem>
		</Group>}

        {fetchedUser &&
		<Group title="Данные аккаунта">
			<Div>
				<img className="Persik" src={fetchedUser.photo_max_orig} alt="Persik The Cat"/>
			</Div>
		</Group>}


		<Group title="Navigation Example">
			<Div>
				<Button size="xl" level="2" onClick={go} data-to="persik">
					
					Показать Персика 
				</Button>
			</Div>
		</Group>
	</Panel>
);


Home.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
	fetchedUser: PropTypes.shape({
		photo_200: PropTypes.string,
		first_name: PropTypes.string,
		last_name: PropTypes.string,
		city: PropTypes.shape({
			title: PropTypes.string,
		}),
	}),
};


export default Home;


